.movie-block {
	@include lg {
		height: 100%;
		margin: 0 20px; } }
.movie-block:last-child {
	@include md {
		margin-right: 21px; } }
.movie-block:first-child {
	@include md {
		margin-left: 21px; } }
.movie-block p {
	margin-bottom: 30px;
	@include md {
		font-size: 14px; } }
#movies {
	.container-new {
		@include md {
			padding-top: 20px;
			padding-bottom: 20px; } } }
